import React, { Fragment, useEffect, useState } from 'react'
import http from '../helper/http'
import { Card } from '../home/Card'
import { BanderaCategoria } from '../productos/BanderaCategoria'

export const IndexOfertas = () => {
    const [resultado,setResultado] = useState(null)
    useEffect(()=>{
        http(`http://localhost:4000/api/ofertas`,).then(res => setResultado(res))
    },[])

    return (
        <Fragment>
            <section>
                <div>
                    <BanderaCategoria categoria="OFERTAS" />
                    <div className="grid-destacados">
                        {resultado && resultado.map(element => <Card key={element.id} element={element}/>)}
                    </div>
                </div>
            </section>

        </Fragment>
    )
}
