import React, { useEffect, useState } from 'react'

export const useMediaQuery = (breakpoint) => {
    const [isTrue,setIsTrue] = useState(false)
    useEffect(()=>{
        // agregamos un evento para el header
        window.addEventListener("resize",()=> {
            (window.screen.availWidth > breakpoint)? setIsTrue(true) : setIsTrue(false)
        })
        // por unica vez hacemos esto al recargar la pagina.
        if (window.screen.availWidth > breakpoint){
            setIsTrue(true)
        }

    },[])
    return isTrue
}
