import './IndexRegister.scss'
import React from "react"
import { Form ,Field, Formik, ErrorMessage} from "formik"
import * as yup from 'yup'
import { Titulo } from '../Titulo'
import http from '../helper/http'
const IndexRegister = () => {

    return (
         <Formik 
         initialValues={{
            nombre: "",
            apellido: "",
            email: "",
            password: "",
            repPassword: ""
         }}
         validationSchema={
            yup.object().shape({
                nombre: yup.string()
                .matches(/[a-zA-Z]+/i,)
                .max(100)
                .required("Este campo no puede estar vacio"),
                apellido: yup
                .string()
                .max(100)
                .required("Este campo no puede estar vacio"),
                email: yup
                .string()
                .email()
                .required("Este campo no puede estar vacio"),
                password: yup
                .string()
                .min(6,"La contraseña debe contener al menos 6 caracteres")
                .required("Este campo no puede estar vacio"),
                repPassword: yup
                .string()
                .required("Este campo no puede estar vacio")
                .oneOf([yup.ref("password")],"Las contraseñas deben ser iguales")
            })
         }
         onSubmit={(formdata)=>{
            http(`${process.env.REACT_APP_API_URL}/register`,{
                method: "POST",
                headers: {
                    "content-type": "application/json"
                },
                body: JSON.stringify(formdata)
            }).then(res=> console.log(res))
         }}
         
         
         >
             {
                 ()=> {

                    return (
                        <div className="contenedor-register">
                        <Titulo message="Registro"/>
                        <span>* No colocar datos reales</span>
                        <Form>
                            <div className="form-group">
                                <label>Nombre</label>    
                                <Field type="text" name="nombre" placeholder="Introduzca su Nombre"  />
                                <ErrorMessage className="error" component="span" name="nombre" />        
                            </div>
                            <div className="form-group" >
                                <label>Apellido</label>    
                                <Field type="text" name="apellido" placeholder="Introduzca su Apellido"/>
                                <ErrorMessage className="error" component="span" name="apellido" />                
                            </div>
                            <div className="form-group" >
                                <label>Email</label>    
                                <Field type="email" name="email" placeholder="Ej: example@outlook.com"/> 
                                <ErrorMessage className="error" component="span" name="email" />               
                            </div>
                            <div className="form-group" >
                                <label>Contraseña</label>    
                                <Field type="password" name="password" placeholder="Introduzca su Contraseña" />
                                <ErrorMessage className="error" component="span" name="password" />                
                            </div>
                            <div className="form-group">
                                <label>Repetir Contraseña</label>    
                                <Field type="password" name="repPassword" placeholder="Repita su Contraseña" />
                                <ErrorMessage className="error" component="span" name="repPassword" />                
                            </div>
                            <div className="submit-register">
                                <button type="submit">Registrarse</button>
                            </div>
                            
                        </Form>
                        
                    </div>
                    )
                 }
             }
         </Formik>
       
    )
}

export default IndexRegister
