import './App.scss'

import { BrowserRouter as Router, Switch,Route, HashRouter  } from 'react-router-dom';
import { IndexHome } from './componentes/home/IndexHome';
import { IndexProductos } from './componentes/productos/IndexProductos';
import { IndexCarrito } from './componentes/carrito/IndexCarrito';
import { IndexNosotros } from './componentes/nosotros/IndexNosotros';
import { Layout } from './componentes/layout/Layout';
import IndexRegister from './componentes/register/IndexRegister';
import IndexLogin from './componentes/login/IndexLogin';
import { IndexOfertas } from './componentes/ofertas/IndexOfertas';
import { IndexProducto } from './componentes/producto/IndexProducto';
import { useEffect } from 'react';
import { useSelector } from 'react-redux';

function App() {
  const state = useSelector((state)=> state.reducer_product)
  
  useEffect(()=>{
    sessionStorage.setItem("product",JSON.stringify(state.product))
    sessionStorage.setItem("ids",JSON.stringify(state.ids))
  },[state])

  return (
    <HashRouter 
    forceRefresh={true}>
      <Layout>
        <Switch>
            <Route path='/productos/:id' component={IndexProducto}/>
            <Route path='/nosotros' component={IndexNosotros}/>
            <Route path='/ofertas' component={IndexOfertas}/>
            <Route path='/carrito' component={IndexCarrito}/>
            <Route path='/productos' component={IndexProductos}/>
            <Route path='/register' component={IndexRegister}/>
            <Route path='/login' component={IndexLogin}/>
            <Route path='/' component={IndexHome}/>
          
        </Switch>
      </Layout>
              
    </HashRouter>
    
  );
}

export default App;
