import React from 'react'
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import cart from '../../images/cart.svg';

export const Carrito = () => {
    const productos = useSelector((state)=> state.reducer_product.product)

    return (
        <div className="content_carrito_user" style={{display: "flex"}}>
            <Link to="/carrito" className="carrito" style={{position: "relative"}}>
                <img src={cart} alt="Imagen de un icono de carrito de compras" />
                {(productos.length > 0) && <span style={{width: "9px",height: "9px",borderRadius: "50%",background: "#32ec3e",position: "absolute", bottom: "12px",left : "0",color:"#111"}} className="indicador"></span>}
            </Link>
        </div>
    )
}
