import '../register/IndexRegister.scss'
import React from "react"
import { Form ,Field, Formik, ErrorMessage} from "formik"
import * as yup from 'yup'
import http from '../helper/http'

const IndexLogin = () => {

    return (
         <Formik 
         initialValues={{
            email: "",
            password: ""
         }}
         validationSchema={
            yup.object().shape({
                email: yup
                .string()
                .email()
                .required("Este campo no puede estar vacio"),
                password: yup
                .string()
                .min(6,"La contraseña debe contener al menos 6 caracteres")
                .required("Este campo no puede estar vacio")
            })
         }
         onSubmit={(formdata)=>{
            http(`${process.env.REACT_APP_API_URL}/login`,{
                method: "POST",
                headers: {
                    "content-type": "application/json"
                },
                body: JSON.stringify(formdata)
            }).then(res=> (res.token) && localStorage.setItem("token",res.token))
         }}
         
         
         >
             {
                 ()=> {

                    return (
                        <div className="contenedor-formulario">
                        <h2>Login</h2>
                        <span>* No colocar datos reales</span>
                        <Form>
                            <div className="form-group" >
                                <label>Email</label>    
                                <Field type="email" name="email" placeholder="Ej: example@outlook.com"/> 
                                <ErrorMessage className="error" component="span" name="email" />               
                            </div>
                            <div className="form-group" >
                                <label>Contraseña</label>    
                                <Field type="password" name="password" placeholder="Introduzca su Contraseña" />
                                <ErrorMessage className="error" component="span" name="password" />                
                            </div>
                            <div className="submit-formulario">
                                <button type="submit">Entrar</button>
                            </div>
                            
                        </Form>
                        
                    </div>
                    )
                 }
             }
         </Formik>
       
    )
}

export default IndexLogin
